*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;   
}

#logo{
    display: flex;
    text-align: center;
    width: 100%;
    height: 180px;
    
}
.l2{
    width: 77%;
    color: red;
    background-color: aqua;
    padding-top: 35px;
    
}
.l2>h1{
  padding-top: 5px;
  font-size: 36px;
  width: 100%;
}
.l1{
    width: 11.5%;
}
#nav{
    display: flex;
    background-color: green; 
}
.n1{
    width: 11.5%;
    background-color: white;
}
#n2{
    display: flex;
    justify-content: space-around;
    width: 77%;
    background-color: red;
    box-shadow: 0px 3px 4px black;
}
#n2>div{
    background-color: green;
    width: 14%;
    padding: 15px;
    display: flex;
    justify-content: center;
}
#n2>div:hover{
     background-color: blue;   
}
.na{
    color: white;
    font-size: 21px;
}
.na:hover{
    color: black;
}
#logo2{
    font-size: x-large;
    display: flex;
    margin-top: 1.5%;
}
#l25{
    width: 100%;
    color: blue;
    text-align: center;
}
#l25>h1{
  padding-top: 20px; 
}
.l21{
    width: 11.5%;
}

#j{
    margin-top: 2.5%;
    width: 77%;
    margin-left: 11.5%;
}
#job{
    margin-top: 1.2%;
    justify-content: space-around;
    display: flex;
}
.job1{
    background-color: violet;
    width: 24%;
    height: 60px;
    border: 1px solid black;
    box-shadow: 3px 4px blue;
    text-align: center;
}
.job2{
    text-align: center;
    background-color: palegreen;
    width: 24%;
    height: 60px;
    border: 1px solid black;
    box-shadow: 3px 4px blue;
}
.job1:hover{
    background-color: green;
    box-shadow: 3px 4px black;
    
 }
.job2:hover{
     background-color: green;
     box-shadow: 3px 4px black;
 }
.job3{
    color: black;
    text-decoration: none;
}
.job3:hover{
    color: white;
}
#con1{
    width: 77%;
    height: auto;
    margin-left: 11.5%;
    margin-top: 3%;
    
}
#con1>div{
    margin-top: 3%;
    display: flex;
    justify-content: space-between;
    height: auto;
    
}
#con1>div>div{
    width: 32.7%;
    height: auto;
    overflow: hidden;
    border: 2px solid blue;
    box-shadow: 0px 3px 0px blue;
}
#con1>div>div>h1{
    padding: 7px;
    width: 100%;
    text-align: center;
    color: white;
    background-color: blue;
}
#con2{
    height: 900px;
    padding-left: 15px;
    
}
#con2>ul{
  padding-left: 6px;
  
}
#con2>ul>li{
    width: 300px;
    margin-top: 5%;
}
#con2>ul>li>a{
  text-decoration: none;
  color: black;
  font-size: large;
  font-weight: bolder;
  
}
#con2>ul>li>a:hover{
    color: green;
}

#view{
    height: 3.5%;
    padding-top: 1.3%;
    margin-left: 65%;
    margin-top: 22%;  
    margin-right: 5%;
    margin-bottom: 7%;
    border: 1px solid black;
    background-color: green;
    box-shadow: 2px 3px 3px black;
}
#view:hover{
    border: 1px solid blue;
    background-color: yellow;
    box-shadow: 2px 3px 3px blue;
}
.view1{
    font-style: italic;
    font-family: sans-serif;
    color: white;
    font-weight: bolder;
    text-align: center;
    padding-top: 2px;
}
.view1:hover{
    color: black;
}
#logo1{
    width: 100%;
    padding-left: 11.4%;
}
#logo1>div{
    display: flex;
    padding-top: 40px;
    height: auto;
    width: 100%;
}
#logo1>div>h1{
    width: 19%;
    color: red;
    font-weight: bolder;
}
#logo1>div>div{
    text-align: center;
    width: 12.33%;
    height: 50px;
    margin-left: 1.25%;
    padding-top: 12px;
    border: 1px solid black;
    box-shadow: 3px 4px blue;
    background-color: aqua;
    font-weight: bolder;
    font-size: 19px;
}
#logo1>div>div:hover{
    box-shadow: 3px 4px black;
    background-color: green;
}
#ab3{
    height: auto;
    width: 77%;
    margin-top: 50px;
    margin-left: 11.5%;
}
#ab3>div>section{
    height: 2px;
    width: 100%;
    background-color: black;
    box-shadow: inset 1px 0px 5px black;
}
#footer{
    background-color: rgb(255, 212, 242);
    padding-bottom: 20px;
    height: auto;
    display: flex;
}
#footer>div{
  background-color: rgb(255, 212, 242);
    height: auto;
    width: 33%;
    margin-top: 2%;
    margin-left: 2%;
    padding-left: 40px;
}
#footer>div>p{
    margin-top: 1%;
}
#footer>div>div{
  margin-top: 1%;
  font-size: 18px;
}
#des{
  width: 77%;
  margin-left: 11.5%;
  margin-top: 50px;
}
#des>h1{
  padding: 7px;
  width: 100%;
  color: white;
  background-color: red;
  font-size: xx-large;
  font-weight: bolder;
  text-align: center;
  margin-top: 20px;
  margin-bottom: 40px;
}
#des>div{
  border: 2px solid green;
  padding-bottom: 30px;
}
#des>div>h1{
  padding: 4px;
  color: white;
  background-color: green;
  text-align: center;
  margin-bottom: 25px;
}
#des>div>h3{
  padding-bottom: 7px;
   padding-left: 30px;
}
#des>div>li{
  padding-left: 130px;
  padding-top: 8px;
}
