#list{
    height: auto;
    width: 77%;
    background-color:white;
    margin-left: 11.5%;
    margin-top: 30px;
    padding-bottom: 30px;
    border: 2px solid rgb(155, 248, 143);
}
#list>h1{
    color: white;
    width: 100%;
    height: 40px;
    /* background-color: rgb(62, 224, 189); */
    background-color: cadetblue;
    padding-left: 41%;
}
#list>h3{
    color: black;
    margin-top: 30px;
    background-color: rgb(155, 248, 143);
    padding-left: 34%;
    margin-bottom: 30px;
}
#list>li{
    padding-left: 55px;
    margin-top: 10px;
}
.a{
    color:  blue;
    font-size: large;
    font-weight: bolder;
}
.a:hover{
    color: black;
}
#page{
    width: 77.5%;
    margin-top: 4%;
    margin-left: 11.5%;
}
#page>h1{
    color: blue;
    padding-left: 22%;
    padding-bottom: 40px;
}
#page>h2{
    padding-bottom: 5px;
}
#page>div{
    border: 1px solid black;
}
#aa{
    display: flex;
    justify-content: center;
    margin: 60px;
    height: auto;
}
#aa>h1{
    color: red;
    font-size: xxx-large;
}
#p1{
    padding: 30px;
    text-align: center;
    width: 100%;
    height: auto;
    border: 1px solid black;
}
#p1>h1{
    color: blue;
}
#p1>h2{
    padding-top: 10px;
    color: rgb(255, 21, 204);
}
#p1>h3{
    padding-top: 10px;
    color: green;
}
#p2{
    font-size: large;
    color: red;
    width: 100%;
    padding: 20px;
    text-align: center;
    border: 1px solid black;
}
#p3{
    width: 100%;
    height: auto;
    display: flex;
}
#p3>div{
    width: 50%;
    border: 1px solid black;
    padding-bottom: 30px;
}
#p3>div>h1{
    text-align: center;
    padding-top: 20px;
    padding-bottom: 20px;
    color: green;
}
#p3>div>p{
   padding-top: 5px;
   padding-left: 35px;
   font-size: x-large;
}
#p4{
    border: 1px solid black;
    padding: 35px;
    width: 100%;
    height: auto;
    
}
#p4>h1{
    color: rgb(255, 21, 204);
    padding-bottom: 10px;
}
#p4>h3{
    padding-top: 10px;
    padding-left: 60px;
}
#p5>h1{
    color: blue;
    text-align: center;
    padding: 25px;
    border: 1px solid black;
}
#p5>div{
    padding: 35px;
    border: 1px solid black;
}
#p5>div>h1{
    color: rgb(255, 21, 204);
    padding-bottom: 10px;
}
#p5>div>h2{
    color: green;
    padding-bottom: 10px;
}
#p5>div>h3{
    /* color: green; */
    padding-top: 5px;
    padding-left:30%;
}
#p6{
    text-align: center;
    color: red;
    border: 1px solid black;
    padding: 25px;
}
#p7{
    border: 1px solid black;
    padding: 35px;  
}
#p7>h1{
    color: rgb(255, 21, 204);
    padding-bottom: 10px;
}
#p7>h3{
    padding-top: 5px;
    padding-left: 30%;
}
#p8>div{
    text-align: center;
    display: flex;
    justify-content: center;
    padding: 25px;
    border: 1px solid black;
}
#p9>div{
    display: flex;
}
#p9>div>div{
    display: flex;
    justify-content: center;
    width: 100%;
    padding: 25px;
    border: 1px solid black;
}
#p9>div>div>h2{
    font-size: 30px;
    color: red;
}
#p9>div>div>a{
    color: blue;
}
#ins{
    text-align: center;
    margin: 50px;
}
#ins>h1{
    padding-bottom: 20px;
    color: red;
    font-size: 40px;
}
@media only screen and (max-width: 723px) {
    #list{
        margin-left: 0;
        margin-top: 30px;
        width: 100%;
     }
     #list>li{
        padding-left: 20px;
     }
     #list>h3{
        padding-left: 14%;
     }
     #list>h1{
        padding-top: 2px;
        font-size: 29px;
        padding-left: 32%;
     }
     .a{
        font-size: 14px;
     }
     
     #page>h1{
        padding-top: 10px;
        padding-bottom: 20px;
        padding-left: 0px;
        text-align: center;
        font-size: 15px;
     }
     #page>h2{
        font-size: 13px;
     }
     #aa{
        margin-top: 10px;
        margin-bottom: 20px;
     }
     #aa>h1{
        font-size: 30px;
     }
     #p1{
        padding: 15px;
        font-size: 8px;
     }
     #p2{
        font-size: 10px;
        padding: 15px;
     }
     #p3>div>h1{
        font-size: 15px;
     }
     #p3>div>p{
        padding-left: 5%;
        font-size: 10px;
     }
     #p4{
        padding: 15px;
        font-size: 8px;
     }
     #p5>h1{
        font-size: 10px;
        padding: 10px;
     }
     #p5>div{
        font-size: 8px;
        padding: 15px;
     }
     #p5>div>h3{
        padding-left: 20%;
     }
     #p6{
        padding: 10px;
        font-size: 8px;
     }
     #p7{
        padding: 15px;
        font-size: 8px;
     }
     #p7>h3{
        padding-left: 20%;
     }
     #p8>div{
        padding: 13px;
        font-size: 9px;
     }
     #p9>div>div>h2{
        font-size: 17px;
     }
     #p9>div>div{
        text-align: center;
        padding: 12px;
        font-size: 10px;
     }
     #ins{
        margin-top: 15px;
        font-size: 10px;

     }
     #ins>h1{
         font-size: 30px;
     }
}

@media (max-width: 1350px) and (min-width: 980px) {
     #list{
        margin-left: 0;
        margin-top: 30px;
        width: 100%;
     }
     #list>li{
        padding-left: 40px;
     }
     #page>h1{
        font-size: 30px;
     }
     #page>h2{
        font-size: 20px;
     }
     #aa{
        margin: 40px;
        margin-top: 20px;
        font-size: 35px;
     }
     #p1{
        font-size: 15px;
     }
     #p2{
        font-size: 17px;
     }
     #p3>div>h1{
        font-size: 30px;
     }
     #p3>div>p{
        font-size: 20px;
     }
     #p4{
        font-size: 15px;
     }
     #p5>h1{
        font-size: 25px;
     }
     #p5>div{
        font-size: 15px;
     }
     #p6{
        font-size: 13px;
     }
     #p7{
        font-size: 15px;
     }
     #p8>div{
        font-size: 15px;
     }
}

@media (max-width: 980px) and (min-width: 724px) {
    #list{
        margin-left: 0;
        margin-top: 30px;
        width: 100%;
     }
     #list>li{
        padding-left: 30px;
     }
     #page>h1{
        padding-top: 20px;
        padding-bottom: 20px;
        padding-left: 2px;
        text-align: center;
        font-size: 21px;
     }
     #page>h2{
        font-size: 15px;
     }
     #aa{
        color: green;
        margin: 30px;
        margin-top: 20px;
        font-size: 20px;
     }
     #p1{
        font-size: 12px;
     }
     #p2{
        font-size: 15px;
        padding: 15px;
     }
     #p3>div>h1{
        font-size: 28px;
     }
     #p3>div>p{
        padding-left: 5%;
        font-size: 18px;
     }
     #p4{
        font-size: 10px;
     }
     #p5>h1{
        font-size: 15px;
        padding: 20px;
     }
     #p5>div{
        font-size: 10px;
     }
     #p5>div>h3{
        padding-left: 20%;
     }
     #p6{
        font-size: 10px;
     }
     #p7{
        font-size: 10px;
     }
     #p7>h3{
        padding-left: 20%;
     }
     #p8{
        font-size: 10px;
     }
     #p9>div>div>h2{
        font-size: 28px;
     }
     #ins{
        margin-top: 15px;
        font-size: 10px;

     }
}