#list{
    height: auto;
    width: 77%;
    background-color:white;
    margin-left: 11.5%;
    margin-top: 30px;
    padding-bottom: 30px;
    border: 2px solid rgb(155, 248, 143);
}
#list>h1{
    color: white;
    width: 100%;
    height: 40px;
    /* background-color: rgb(62, 224, 189); */
    background-color: cadetblue;
    padding-left: 41%;
}
#list>h3{
    color: black;
    margin-top: 20px;
    background-color: rgb(155, 248, 143);
    padding-left: 34%;
}
#list>li{
    padding-left: 55px;
    margin-top: 10px;
}
.a{
    color:  blue;
    font-size: large;
    font-weight: bolder;
}
.a:hover{
    color: black;
}
#page{
    width: 77.5%;
    margin-top: 4%;
    margin-left: 11.5%;
}
#page>h1{
    padding-left: 22%;
    padding-bottom: 40px;
}
#page>h2{
    padding-bottom: 5px;
    padding-left: 2%;
}
#page>div{
    width: 80%;
    margin-left: 10%;
    margin-top: 60px;
    height: auto;
    border: 2px solid black;
}
#p1{
    padding: 30px;
    text-align: center;
    width: 100%;
    height: auto;
    border: 2px solid blue;
}
#p2{
    width: 100%;
    height: 80px;
    padding-top: 16px;
    text-align: center;
    border: 2px solid blue;
}
#p3{
    width: 100%;
    height: auto;
    border: 2px solid blue;
    display: flex;
}
#p3>div{
    width: 50%;
    border: 2px solid blue;
}
#p3>div>h1{
    text-align: center;
}
#p4{
    padding-top: 25px;
    width: 100%;
    height: auto;
}
#p4>h1{
    padding-left: 20px;
    padding-bottom: 10px;
}
#p4>h3{
    padding-top: 10px;
    padding-left: 60px;
}
@media only screen and (max-width: 723px) {
    #list{
        margin-left: 0;
        margin-top: 30px;
        width: 100%;
     }
     #list>li{
        padding-left: 20px;
     }
     #list>h3{
        padding-left: 14%;
     }
     #list>h1{
        padding-top: 2px;
        font-size: 29px;
        padding-left: 19%;
     }
     .a{
        font-size: 14px;
     }
}

@media (max-width: 1350px) and (min-width: 980px) {
     #list{
        margin-left: 0;
        margin-top: 30px;
        width: 100%;
     }
     #list>li{
        padding-left: 40px;
     }
}

@media (max-width: 980px) and (min-width: 724px) {
    #list{
        margin-left: 0;
        margin-top: 30px;
        width: 100%;
     }
     #list>li{
        padding-left: 30px;
     }
}